declare var $: any
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HttpServicesProvider } from '../../../../providers/http-services';
import { Utils } from '../../../../providers/Utils';
import { StorageProvider } from '../../../../providers/storage';
import { PhoneDeviceProvider } from '../../../../providers/phone-device';

/**
 * Generated class for the LmtagprojectloglistPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lmtagprojectloglist',
  templateUrl: 'lmtagprojectloglist.html',
})
export class LmtagprojectloglistPage {

  public tabNameList = [{ 'name': '我的', 'code': '', 'value': '' }, { 'name': '已完成', 'code': '', 'value': '' }, { 'name': '未完成', 'code': '', 'value': '' }]
  
  public id = '';
  public list = []; //列表
  public dataList = []; //缓存页码数据（上拉加载）
  public mCurrentPage = 0; //默认page
  public where = ''; //搜索
  public description = ''; //搜索输入框内容
  public searchlist = [];
  public stops = ''; //无数据加载完毕
  public currentTab = 0; //tab默认第一个
  public itemTab = 99;
  public status = '';
  public images = [];
  public lmprojectsourcelist = [];
  public projectid = '';
  public timer = null;


  constructor(public navCtrl: NavController, public storage: StorageProvider, public navParams: NavParams, public httpService: HttpServicesProvider,
    public phoneDevice: PhoneDeviceProvider) {
    this.phoneDevice.phoneBacktoPop(this.navCtrl);
    this.projectid = this.storage.get(StorageProvider.LM_PROJECTID);

    this.id = this.navParams.get('prjid');

  }
  ionViewWillEnter() {//当将要进入页面时触发
    this.list = [];
    this.initlist(1);
  }

  // 搜索+防抖
  handleSearch() {
    this.list = [];
    clearTimeout(this.timer)
    if (this.description == '') {
      this.initlist(1);
      return false;
    }
    this.timer = setTimeout(() => {
      var fuzzysearch = encodeURIComponent(this.description);
      var url = '/lmtagProjectLog/list?prjid=' + this.id + '&rows=-1&page=1&sidx=&sord=code&sord=asc&fuzzysearch=' + fuzzysearch;
      this.initsearchlist(url);
      this.timer = null;
    }, 300)
  }

  clickInfo(id) {//查看详情
    this.description = '';
    this.searchlist = [];
    this.list = [];
    this.navCtrl.push('LmtagprojectlogPage', {
      type: 'edit',
      logid: id,
    });
  }
  addClick() {//新建
    this.navCtrl.push('LmtagworkorderinfoPage', {
      type: 'add'
    })
  }
  initsearchlist(url) {//初始化搜索列表
    var that = this;
    $('.tab-nav').hide();
    that.httpService.get(url, null, (result) => {
      if (result != null && result.rows.length > 0) {
        var rows = result.rows;
        that.list = rows;
      } else {
        $('.searchWarp .listWarp').html('没有搜索到相关数据').css({ 'text-align': 'center' });
      }
    });
  }
  initlist(page) {//初始化列表
    var url = '';

    if (this.currentTab == 0) {
      url = '/lmtagProjectLog/list?prjid=' + this.id + '&sort=entertime&_search=false&rows=20&page=' + page + '&sidx=&sord=asc'
    }
    if (this.currentTab == 1) {
      url = '/lmtagProjectLog/list?prjid=' + this.id +  + '&sort=code&_search=false&rows=20&page=' + page + '&sidx=&sord=asc&nw_status=1'
    }
    if (this.currentTab == 2) {
      url = '/lmtagProjectLog/list?prjid=' + this.id +  + '&sort=code&_search=false&rows=20&page=' + page + '&sidx=&sord=asc&nw_status=2'
    }
    var that = this;
    var lists = this.list;
    that.httpService.get(url, null, (result) => {
      if (page > result.total) {
        this.stops = 'true';
        return false;
      }
      if (result != null && result.rows.length > 0) {
        var rows = result.rows;
        that.dataList = rows;
        lists = lists.concat(that.dataList);
        that.list = lists;
        that.mCurrentPage = page;
      }
    });
  }
  chooseStatus(value, index) {
    $('.tab-nav .nav-item:eq(' + index + ')').addClass('active').siblings().removeClass('active');
    $('.screenBox .item').removeClass('active');
    if (this.currentTab == index) {
      return false;
    } else {
      this.currentTab = index;
    }
    this.list = [];
    this.initlist(1)
  }
  doRefresh(refresher) {//下拉刷新
    this.list = [];
    this.lmprojectsourcelist = [];
    this.initlist(1)
    setTimeout(() => {
      refresher.complete(); //停止下拉刷新
    }, 2000)
  }
  doInfinite(infiniteScroll) {//上拉加载
    this.initlist(this.mCurrentPage + 1)
    infiniteScroll.complete();//停止上拉加载
    if (this.stops == 'true') {
      infiniteScroll.enable(false);//停止上拉加载
    }
  }
}



